import { Col, Input, Row, Typography } from "antd";
import { useAtom } from "jotai";
import { wowRoles } from "../../lib/groups/wow-groups";
import { updateString } from "../../lib/string";
import { expectAtom } from "../../lib/url-state/atoms";
import { DiscordIcon } from "../DiscordIcon";

export function SetupContent() {
  const [expects, setExpects] = useAtom(expectAtom);

  function handleExpectChanges(index: number, value: string) {
    setExpects(updateString(expects ?? "", index, Number(value)));
  }

  const countR25 =
    Number(expects[0]) +
    Number(expects[1]) +
    Number(expects[2]) +
    Number(expects[3]) +
    Number(expects[4]);
  const countR10 =
    Number(expects[5]) +
    Number(expects[6]) +
    Number(expects[7]) +
    Number(expects[8]) +
    Number(expects[9]);

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        margin: "auto",
      }}
    >
      <Row gutter={8}>
        <Col span={12}>
          <Typography.Title level={4}>
            Raid 25 <span style={{ fontSize: "0.8em" }}>({countR25})</span>
          </Typography.Title>
          {Object.keys(wowRoles).map((wowRole, index) => {
            const realIndex = index;
            return (
              <Input
                key={`expect-config-r25-${realIndex}`}
                type="number"
                onChange={(e) => handleExpectChanges(realIndex, e.target.value)}
                value={expects.charAt(realIndex)}
                prefix={
                  <div style={{ minWidth: 100 }}>
                    <DiscordIcon code={wowRoles[wowRole]} />
                    {wowRole} :
                  </div>
                }
              />
            );
          })}
        </Col>{" "}
        <Col span={12}>
          <Typography.Title level={4}>
            Raid 10 <span style={{ fontSize: "0.8em" }}>({countR10})</span>
          </Typography.Title>
          {Object.keys(wowRoles).map((wowRole, index) => {
            const realIndex = index + 5;
            return (
              <Input
                key={`expect-config-r10-${realIndex}`}
                type="number"
                onChange={(e) => handleExpectChanges(realIndex, e.target.value)}
                value={expects.charAt(realIndex)}
                prefix={
                  <div style={{ minWidth: 100 }}>
                    <DiscordIcon code={wowRoles[wowRole]} />
                    {wowRole} :
                  </div>
                }
              />
            );
          })}
        </Col>
      </Row>
    </div>
  );
}
