import { ClearOutlined, PlusCircleOutlined } from "@ant-design/icons";
import { Button, Popover, Input } from "antd";
import { useState } from "react";
import { PopoverContent } from "./PopoverContent";

interface Props {
  currentRaidCount: number;
  addRaid: (raidName: string) => void;
}

export function RaidAddFields({ currentRaidCount, addRaid }: Props) {
  const [raidNameToAdd, setRaidNameToAdd] = useState("");
  const isMaxRaidsReached = currentRaidCount >= 10;

  return (
    <div style={{ display: "flex" }}>
      <Button
        onClick={() => setRaidNameToAdd("")}
        style={{ borderTopRightRadius: 0, borderBottomRightRadius: 0 }}
      >
        <ClearOutlined />
      </Button>
      <Popover
        title="Quick add text for raid name"
        placement="topRight"
        content={
          <PopoverContent
            raidNameToAdd={raidNameToAdd}
            setRaidNameToAdd={setRaidNameToAdd}
          />
        }
      >
        <Button
          onClick={() => {
            addRaid(raidNameToAdd);
            setRaidNameToAdd("");
          }}
          disabled={isMaxRaidsReached}
          style={{ borderRadius: 0, marginRight: -1, marginLeft: -1 }}
        >
          <PlusCircleOutlined />
          Add Raid
        </Button>
      </Popover>

      <Input
        style={{ borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }}
        placeholder={isMaxRaidsReached ? "Max raid reached" : "Raid name"}
        maxLength={60}
        value={raidNameToAdd}
        onChange={(e) => setRaidNameToAdd(e.target.value)}
      />
    </div>
  );
}
