import { CameraOutlined } from "@ant-design/icons";
import { Button, message } from "antd";
import html2canvas from "html2canvas";

interface Props {
  setIsScreenshotMode: (isScreenshotMode: boolean) => void;
}

export function ScreenshotButton({ setIsScreenshotMode }: Props) {
  function takeScreenshot() {
    setIsScreenshotMode(true);
    setTimeout(() => {
      html2canvas(document.getElementById("screenshotZone")!, {
        useCORS: true,
      })
        .then(function (canvas) {
          canvas.toBlob((blob) => {
            const item = new ClipboardItem({ "image/png": blob! });
            navigator.clipboard.write([item]);
          });
        })
        .then(() => {
          setTimeout(() => {
            setIsScreenshotMode(false);
            message.success("The screenshot had been copied to your clipboard");
          }, 300);
        })
        .catch(() => {
          message.error("An error occured, try manual print screen");
        });
    }, 300);
  }
  return (
    <Button onClick={takeScreenshot}>
      <CameraOutlined />
      Take screenshot
    </Button>
  );
}
