import { Button, Drawer, Space } from "antd";
import { useAtom } from "jotai";
import { useState } from "react";
import { raidsAtom } from "../../lib/url-state/atoms";

export function GroupsDrawer() {
  const [visible, setVisible] = useState(false);
  const [raids] = useAtom(raidsAtom);

  return (
    <>
      <Space>
        <Button onClick={() => setVisible(true)}>Show groups</Button>
      </Space>

      <Drawer
        title={"Groups"}
        placement="right"
        onClose={() => setVisible(false)}
        visible={visible}
      >
        {raids.map((raid, raidIndex) => {
          return <div>{raid}</div>;
        })}
      </Drawer>
    </>
  );
}
