import React from "react";

interface Props {
  max: number | string;
  value: number | string;
  disableMax?: boolean;
  style?: React.CSSProperties;
}

export function PlayerCounter({ disableMax, style, ...rest }: Props) {
  const max = Number(rest.max);
  const value = Number(rest.value);

  let colorValue = "white";
  let colorMax = "white";
  if (disableMax || max === 0) {
    colorValue = "white";
    colorMax = "white";
  } else if (value > max) {
    colorValue = "red";
  } else if (value === max) {
    colorValue = "green";
    colorMax = "green";
  } else {
    colorValue = "orange";
  }

  return (
    <span style={{ fontSize: "0.8em", marginLeft: 4, ...style }}>
      {value > 0 &&
        (disableMax || max === 0 ? (
          <>
            {" "}
            - <span style={{ color: colorValue }}>{value}</span>
          </>
        ) : (
          <>
            {" "}
            - <span style={{ color: colorValue }}>{value}</span>
            <span style={{ color: colorMax }}>/{max}</span>
          </>
        ))}
    </span>
  );
}
