import { useAtom } from "jotai";
import { PropsWithChildren } from "react";
import { debugAtom } from "../../lib/url-state/atoms";

export function Debug({
  children,
  ...rest
}: PropsWithChildren<
  React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLSpanElement>,
    HTMLSpanElement
  >
>) {
  const [isDebug] = useAtom(debugAtom);

  const { style, ...props } = rest;

  return (
    <span style={{ ...style, display: isDebug ? "inline" : "none" }} {...props}>
      {children}
    </span>
  );
}
