import { Button, Drawer, Space } from "antd";
import { useAtom } from "jotai";
import { useContext, useState } from "react";
import { RaidHelperContext } from "../../lib/contexts/raidHelper";
import { isLocalEnv } from "../../lib/env";
import { debugAtom } from "../../lib/url-state/atoms";
import { SignupBox } from "../SignupBox";

export function AbsentDrawer() {
  const [visible, setVisible] = useState(false);
  const raidHelper = useContext(RaidHelperContext);
  const [isDebug, setDebug] = useAtom(debugAtom);

  return (
    <>
      <Space>
        {isLocalEnv() && (
          <Button onClick={() => setDebug(!isDebug)}>Debug</Button>
        )}
        <Button onClick={() => setVisible(true)}>Show absents</Button>
      </Space>

      <Drawer
        title={"Absents"}
        placement="right"
        onClose={() => setVisible(false)}
        visible={visible}
      >
        {raidHelper?.signups
          .filter((item) => item.role === "Absence")
          .map((signup) => (
            <div key={`signup-${signup.userid}`}>
              <SignupBox {...signup} />
            </div>
          ))}
      </Drawer>
    </>
  );
}
