export function updateString(
  baseString: string,
  position: number,
  number: number
) {
  return (
    baseString.substring(0, position) +
    number +
    baseString.substring(position + 1)
  );
}

export function fillWithNumber(length: number, number: number = 0) {
  return String(number).repeat(Math.max(length + 1, 0));
}

export function capMaxRaidId(usersRaids: string, maxId: number) {
  for (let i = 0; i < usersRaids.length; i++) {
    const userRaid = Number(usersRaids.charAt(i));
    if (userRaid >= maxId) {
      usersRaids = updateString(usersRaids, i, maxId - 1);
    }
  }
  return usersRaids;
}

export function updateRaidIndex(usersRaids: string, raidCount: number) {
  for (let i = 0; i < usersRaids.length; i++) {
    const userRaid = Number(usersRaids.charAt(i));
    if (userRaid >= raidCount - 3) {
      usersRaids = updateString(usersRaids, i, userRaid + 1);
    }
  }
  return usersRaids;
}

export function updateRaidIndexDelete(
  usersRaids: string,
  raidDeleted: number,
  raidCount: number
) {
  for (let i = 0; i < usersRaids.length; i++) {
    const userRaid = Number(usersRaids.charAt(i));

    const userRaidIsOutsideRange = userRaid > raidCount - 1;
    const userRaidJustBeenDeleted = userRaid === raidDeleted;

    const userRaidIsAfterDeleted = userRaid > raidDeleted;
    // const userRaidIsNotBenchOrBackup = userRaid < raidCount - 2;

    if (userRaidIsOutsideRange || userRaidJustBeenDeleted) {
      // send it to bench
      usersRaids = updateString(usersRaids, i, raidCount - 2);
    } else if (userRaidIsAfterDeleted) {
      // reduce player raid level
      usersRaids = updateString(usersRaids, i, userRaid - 1);
    }
  }
  return usersRaids;
}
