import { SettingOutlined } from "@ant-design/icons";
import { Button, Modal } from "antd";
import { useAtom } from "jotai";
import { useState } from "react";
import { expectAtom, expectAtomdefaultValue } from "../../lib/url-state/atoms";
import { SetupContent } from "./SetupContent";

export function ModalSetup() {
  const [isModalOpen, setModalOpen] = useState(false);
  const [, setExpects] = useAtom(expectAtom);

  function hideModal() {
    setModalOpen(false);
  }

  return (
    <>
      <Button onClick={() => setModalOpen(true)}>
        <SettingOutlined />
      </Button>
      <Modal
        title="Setup your expectations for raid classes"
        visible={isModalOpen}
        onOk={hideModal}
        onCancel={hideModal}
        okText="Apply"
        footer={[
          <Button key="disable" onClick={() => setExpects("0000000000")}>
            Disable
          </Button>,
          <Button
            key="reset"
            onClick={() => setExpects(expectAtomdefaultValue)}
          >
            Reset Values to default
          </Button>,
          <Button key="submit" type="primary" onClick={hideModal}>
            Apply
          </Button>,
        ]}
      >
        <SetupContent />
      </Modal>
    </>
  );
}
