import React, { useContext, useEffect, useRef } from "react";
import {
  DragDropContext,
  Droppable,
  Draggable,
  DropResult,
} from "react-beautiful-dnd";
import { useAtom } from "jotai";
import {
  expectAtom,
  raidsAtom,
  usersRaidAtom,
} from "../../lib/url-state/atoms";
import { DiscordIcon } from "../DiscordIcon";
import { wowGroups, wowRoles } from "../../lib/groups/wow-groups";
import { Signup, SignupWithComponent } from "../../../@types/raid-helper";
import { Button, Input, Typography } from "antd";
import {
  capMaxRaidId,
  updateRaidIndex,
  updateRaidIndexDelete,
  updateString,
  fillWithNumber,
} from "../../lib/string";
import { DeleteOutlined, ImportOutlined } from "@ant-design/icons";

import "./styles.css";
import { SignupBox } from "../SignupBox";
import { RaidHelperContext } from "../../lib/contexts/raidHelper";
import { RaidAddFields } from "../RaidAddFields";
import { Debug } from "../Debug";
import { ModalBuffs } from "../ModalBuffs";
import { PlayerCounter } from "../PlayerCounter";
import { ScreenshotContext } from "../../lib/contexts/screenshotMode";

const grid = 2;
const fixMarginDropable = 34;

const getItemStyle = (
  isDragging: boolean,
  draggableStyle: any
): React.CSSProperties => ({
  padding: grid,
  margin: `0 0 ${grid}px 0`,
  borderRadius: 3,
  background: isDragging ? "#464951" : "#292B2F",
  ...draggableStyle,
});
const getListStyle = (isDraggingOver: boolean): React.CSSProperties => ({
  background: isDraggingOver ? "#34343c" : "#202025",
  padding: grid,
  width: 250,
  marginTop: fixMarginDropable,
});

// inspired by  https://codesandbox.io/s/-w5szl?file=/src/index.js:75-151
export function RaidList() {
  const raidHelper = useContext(RaidHelperContext);
  const [raids, setRaids] = useAtom(raidsAtom);
  const [usersRaid, setUsersRaid] = useAtom(usersRaidAtom);
  const [expects] = useAtom(expectAtom);
  const isScreenshotMode = useContext(ScreenshotContext);
  const initialPlayersCount = useRef(-1);

  console.log("initialPlayersCount :");
  console.log(initialPlayersCount.current);

  //localhost:3000/#raidId=%221008493908847767553%22&usersRaid=%221100000000000000000000000000000000000000000000333%22&raids=%5B%22Raid+25%22%2C%22Raid%22%2C%22Backups%22%2C%22Benchs%22%5D

  const signups: SignupWithComponent[] = (raidHelper?.signups || [])
    .sort((itemA, itemB) => itemA.name.localeCompare(itemB.name))
    .sort((itemA, itemB) =>
      `${itemA.class}${itemA.spec}`.localeCompare(`${itemB.class}${itemB.spec}`)
    )
    .map((signup: Signup) => ({
      component: (id: number) => (
        <SignupBox
          {...signup}
          currentRaidId={id}
          isNew={
            initialPlayersCount.current > 0 &&
            signup.position > initialPlayersCount.current - 1
          }
        />
      ),
      ...signup,
    }));

  function addRaid(raidNameToAdd: string) {
    const newRaids = [...raids];
    const benchs = newRaids.pop()!;
    const backup = newRaids.pop()!;
    newRaids.push(raidNameToAdd || "Raid", backup, benchs);

    setRaids(newRaids);
    const newSetUsersRaid = updateRaidIndex(usersRaid, newRaids.length);
    setUsersRaid(newSetUsersRaid);
  }

  function renameRaid(indexDeleted: number, newName: string) {
    const newRaids = [...raids];
    newRaids[indexDeleted] = newName;

    setRaids(newRaids);
  }

  function deleteRaid(indexDeleted: number) {
    const newRaids = [...raids];
    newRaids.splice(indexDeleted, 1);

    setRaids(newRaids);
    const newSetUsersRaid = updateRaidIndexDelete(
      usersRaid,
      indexDeleted,
      raids.length
    );
    setUsersRaid(newSetUsersRaid);
  }

  function importEveryoneInRaid(raidIndex: number) {
    setUsersRaid(fillWithNumber(signups.length, raidIndex));
  }

  useEffect(() => {
    // manage add to backup for new subscribed members
    const userRaidHasAssignements = usersRaid
      .split("")
      .some((char) => char !== "0");
    const numberToFillWith = userRaidHasAssignements ? raids.length - 1 : 0;

    const newUserRaid = usersRaid
      ? capMaxRaidId(usersRaid, raids.length) +
        fillWithNumber(signups.length - usersRaid.length, numberToFillWith)
      : fillWithNumber(signups.length, numberToFillWith);
    setUsersRaid(newUserRaid);
    if (newUserRaid.length > usersRaid.length) {
      initialPlayersCount.current = usersRaid.length;
    }
  }, [raids, usersRaid, signups, setUsersRaid]);

  function onDragEnd(result: DropResult) {
    const raidDest = Number(result.destination?.droppableId.split("-")[1]!);
    const itemSrcId = Number(result.draggableId.split("-")[1]!);

    if (
      typeof raidDest !== "undefined" &&
      typeof itemSrcId !== "undefined" &&
      !isNaN(raidDest) &&
      !isNaN(itemSrcId)
    ) {
      setUsersRaid(updateString(usersRaid, itemSrcId, raidDest));
    }
  }

  // TODO: faire les liens d'invit groupe automatiquement
  // TODO: fixer les ids react de ModalBuffs
  // TODO: faire un "new" plus styl avec une animation
  // TODO: Compte les classes actuellement prsentes dans le raid ?
  // TODO: enlever la scrollbar globale et n'en mettre une que sur le contenu pour ne pas faire bug les drawers

  return (
    <div>
      {!isScreenshotMode && (
        <RaidAddFields currentRaidCount={raids.length} addRaid={addRaid} />
      )}

      <DragDropContext onDragEnd={onDragEnd}>
        {raids.map((raid, raidIndex) => {
          const filteredSignups = signups
            .filter((item) => Number(usersRaid[item.position]) === raidIndex)
            .filter((item) => item.role !== "Absence");

          const nbMembers = filteredSignups.length;
          const isRaid25 = nbMembers > 15;
          const addToExpectCountIndex = isRaid25 ? 0 : 5;
          const isNotStaticRaids = raid !== "Benchs" && raid !== "Backups";

          return (
            <div
              key={`raid-${raidIndex}}`}
              style={{
                marginTop: 10,
                border: "1px solid #292B2F",
              }}
            >
              <div
                style={{
                  display: "flex",
                  margin: "4px 15px -8px",
                }}
              >
                <Typography.Title level={4}>
                  <Input
                    readOnly={raidIndex > raids.length - 3}
                    onChange={(e) => renameRaid(raidIndex, e.target.value)}
                    value={raid}
                    style={{
                      fontSize: 16,
                      fontWeight: "bold",
                      border: 0,
                      marginLeft: -11,
                      width: 32 + raid.length * 8.2,
                    }}
                  />
                  <PlayerCounter
                    value={nbMembers}
                    max={isRaid25 ? 25 : 10}
                    disableMax={!isNotStaticRaids}
                    style={{ margin: 10 }}
                  />
                </Typography.Title>
                {!isScreenshotMode && (
                  <>
                    <div
                      style={{ marginLeft: 6, marginTop: 5 }}
                      title="Import everyone in this group"
                    >
                      <Button
                        onClick={() => importEveryoneInRaid(raidIndex)}
                        size="small"
                      >
                        <ImportOutlined />
                      </Button>
                    </div>
                    {isNotStaticRaids && (
                      <>
                        <div
                          style={{ marginLeft: 10, marginTop: 5 }}
                          title="Delete this raid"
                        >
                          <Button
                            onClick={() => deleteRaid(raidIndex)}
                            size="small"
                          >
                            <DeleteOutlined />
                          </Button>
                        </div>

                        <div
                          style={{ marginLeft: 60, marginTop: 5 }}
                          title="Check the buffs in this raid"
                        >
                          <ModalBuffs raidIndex={raidIndex} />
                        </div>
                      </>
                    )}
                  </>
                )}
              </div>
              <div style={{ display: "flex", minHeight: 90 }}>
                {Object.keys(wowGroups).map((collumnName, collumnIndex) => {
                  const dropableId = `${collumnIndex}-${raidIndex}-${new Date().getTime()}`;
                  return (
                    <Droppable key={dropableId} droppableId={dropableId}>
                      {(provided, snapshot) => {
                        const groupSignup = filteredSignups.filter((item) =>
                          wowGroups[collumnName].includes(
                            `${item.class}${item.spec}`
                          )
                        );
                        const count = groupSignup.length;
                        const expect = expects.charAt(
                          collumnIndex + addToExpectCountIndex
                        );
                        return (
                          <div
                            ref={provided.innerRef}
                            style={getListStyle(snapshot.isDraggingOver)}
                            {...provided.droppableProps}
                          >
                            <Typography.Title
                              level={5}
                              style={{
                                paddingLeft: 20,
                                marginTop: -fixMarginDropable,
                              }}
                            >
                              <DiscordIcon code={wowRoles[collumnName]} alt />
                              {collumnName}{" "}
                              <PlayerCounter
                                value={count}
                                max={expect}
                                disableMax={!isNotStaticRaids}
                              />
                            </Typography.Title>
                            {groupSignup.map((item, itemIndex) => (
                              <Draggable
                                key={`${item.userid}-${raidIndex}`}
                                // key={`${item.userid}-${item.position}`}
                                draggableId={`${item.userid}-${item.position}`}
                                index={itemIndex}
                              >
                                {(provided, snapshot) => (
                                  <div
                                    ref={provided.innerRef}
                                    {...provided.draggableProps}
                                    {...provided.dragHandleProps}
                                    style={getItemStyle(
                                      snapshot.isDragging,
                                      provided.draggableProps.style
                                    )}
                                  >
                                    {item.component(raidIndex)}
                                    <Debug>
                                      <div>{itemIndex}</div>
                                    </Debug>
                                    <Debug>
                                      {`${item.userid}-${item.position}`}
                                    </Debug>
                                  </div>
                                )}
                              </Draggable>
                            ))}
                            {provided.placeholder}
                          </div>
                        );
                      }}
                    </Droppable>
                  );
                })}
              </div>
            </div>
          );
        })}
      </DragDropContext>
    </div>
  );
}
