import { Layout, Space, Switch } from "antd";
import React, { useState } from "react";
import { RAIDHelper } from "../@types/raid-helper";
import AppLayout from "./components/Layout";
import { RaidList } from "./components/RaidList";
import { RaidHelperContext } from "./lib/contexts/raidHelper";
import { Header } from "antd/lib/layout/layout";
import { AbsentDrawer } from "./components/AbsentDrawer";
import { RaidHelperLoader } from "./components/RaidHelperLoader";
import { RaidTitle } from "./components/RaidTitle";
import { HelperContent } from "./components/ModalHelpRaidId/HelperContent";
import { ScreenshotContext } from "./lib/contexts/screenshotMode";
import FormItem from "antd/es/form/FormItem";
import { PostionDisplayedContext } from "./lib/contexts/positionDisplay";
import { Spacer } from "./components/Spacer";
import { GroupsDrawer } from "./components/GroupsDrawer";

import { ScreenshotButton } from "./components/ScreenshotButton";

function App() {
  const [raidHelper, setRaidHelper] = useState<RAIDHelper>();
  const [isScreenshotMode, setIsScreenshotMode] = useState(false);
  const [isPositionDisplayed, setIsPositionDisplayed] = useState(false);

  return (
    <React.StrictMode>
      <PostionDisplayedContext.Provider value={isPositionDisplayed}>
        <ScreenshotContext.Provider value={isScreenshotMode}>
          <RaidHelperContext.Provider value={raidHelper!}>
            <Layout>
              <Header>
                <h1 className="appTitle">
                  Rai<span>D</span> Roste
                  <span>R</span>
                </h1>
                <RaidHelperLoader setRaidHelper={setRaidHelper} />
                {/* <ModalHelpRaidId /> */}
                <Spacer />
                <Space>
                  <FormItem label="Display numbers" style={{ marginTop: 26 }}>
                    <Switch
                      checked={isPositionDisplayed}
                      onClick={() =>
                        setIsPositionDisplayed(!isPositionDisplayed)
                      }
                    />
                  </FormItem>
                  <FormItem label="Screenshot mode" style={{ marginTop: 26 }}>
                    <Switch
                      checked={isScreenshotMode}
                      onClick={() => setIsScreenshotMode(!isScreenshotMode)}
                    />
                  </FormItem>

                  {raidHelper && (
                    <ScreenshotButton
                      setIsScreenshotMode={setIsScreenshotMode}
                    />
                  )}
                  {raidHelper && <AbsentDrawer />}
                  {raidHelper && <GroupsDrawer />}
                </Space>
              </Header>
              <AppLayout>
                {raidHelper ? (
                  <>
                    <RaidTitle />
                    <RaidList />
                  </>
                ) : (
                  <>
                    <HelperContent />
                  </>
                )}

                {/* <code>
        <pre>{JSON.stringify(raidHelper, null, 2)}</pre>
      </code> */}
              </AppLayout>
            </Layout>
          </RaidHelperContext.Provider>
        </ScreenshotContext.Provider>
      </PostionDisplayedContext.Provider>
    </React.StrictMode>
  );
}

export default App;
