import { StepBackwardOutlined, StepForwardOutlined } from "@ant-design/icons";
import { Typography, Popover, Image, Space, Button } from "antd";
import moment from "moment";
import { useContext } from "react";
import { RaidHelperContext } from "../../lib/contexts/raidHelper";
import { ScreenshotContext } from "../../lib/contexts/screenshotMode";
import { ModalSetup } from "../ModalSetup";

export function RaidTitle() {
  const raidHelper = useContext(RaidHelperContext);
  const { servericon, leadername, title, date } = raidHelper;
  const isScreenshotMode = useContext(ScreenshotContext);

  const [day, month, year] = date.split("-");

  return (
    <Typography.Title level={3}>
      <Space>
        <Image
          preview={false}
          src={servericon}
          style={{
            borderRadius: "50%",
            width: 35,
          }}
        />
        <Popover
          placement="top"
          content={"Raid created by " + leadername}
          trigger="hover"
        >
          {title}{" "}
          <span style={{ fontSize: "0.8em" }}>
            (
            {moment(new Date(`${year}-${month}-${day}`)).format(
              "ddd DD MMM YYYY"
            )}
            )
          </span>
        </Popover>
        {!isScreenshotMode && (
          <>
            <ModalSetup />
            <Button onClick={() => window.history.back()}>
              <StepBackwardOutlined />
            </Button>
            <Button onClick={() => window.history.forward()}>
              <StepForwardOutlined />
            </Button>
          </>
        )}
      </Space>
    </Typography.Title>
  );
}
