import { Image } from "antd";
import { CSSProperties } from "react";

import "./styles.css";

interface Props {
  code: string;
  style?: CSSProperties;
  alt?: boolean;
  className?: string;
}

export function DiscordIcon({
  code,
  className,
  style = {},
  alt = false,
}: Props) {
  const src = code.startsWith("http")
    ? code
    : `https://cdn.discordapp.com/emojis/${code}.webp?size=96&quality=lossless`;
  return (
    <Image
      preview={false}
      className={className}
      style={{
        width: 20,
        marginRight: alt ? 7 : 5,
        marginTop: alt ? -4 : -3,
        ...style,
      }}
      src={src}
    />
  );
}
