import { PlusCircleOutlined, RightSquareOutlined } from "@ant-design/icons";
import { Button } from "antd";
import { PropsWithChildren } from "react";

interface Props extends PropsWithChildren {
  onClick: (text: string) => void;
  alt?: boolean;
}

export function RaidButton({ children, onClick, alt = false }: Props) {
  return (
    <Button onClick={() => onClick(String(children))}>
      {alt ? <RightSquareOutlined /> : <PlusCircleOutlined />}
      {children}
    </Button>
  );
}

export function RaidButton2(props: Props) {
  return <RaidButton {...props} alt></RaidButton>;
}
