import { Tag } from "antd";
import { useAtom } from "jotai";
import { useContext } from "react";
import { Signup } from "../../../@types/raid-helper";
import { PostionDisplayedContext } from "../../lib/contexts/positionDisplay";
import { ScreenshotContext } from "../../lib/contexts/screenshotMode";
import { wowColors } from "../../lib/groups/wow-colors";
import { updateString } from "../../lib/string";
import { raidsAtom, usersRaidAtom } from "../../lib/url-state/atoms";
import { DiscordIcon } from "../DiscordIcon";

import "./styles.css";

interface Props extends Signup {
  currentRaidId?: number;
  isNew?: boolean;
}

export function SignupBox({ currentRaidId, isNew, ...signup }: Props) {
  const calcMargin = signup.position >= 10 ? 3 : 7;
  const isPositionDisplayed = useContext(PostionDisplayedContext);
  const isScreenshotMode = useContext(ScreenshotContext);
  const [raids] = useAtom(raidsAtom);
  const [usersRaid, setUsersRaid] = useAtom(usersRaidAtom);

  function updateToRaidIndex(newIndex: number) {
    const newSetUsersRaid = updateString(usersRaid, signup.position, newIndex);
    setUsersRaid(newSetUsersRaid);
  }

  return (
    <div
      className="signupBox"
      style={{
        display: "flex",
        justifyContent: "start",
        minWidth: 170,
        overflow: "hidden",
        width: isScreenshotMode ? 170 : "100%",
      }}
    >
      <div
        style={{
          fontWeight: "bold",
          color:
            wowColors[signup.class] ??
            wowColors[`${signup.class}${signup.spec}`],
        }}
      >
        <DiscordIcon code={signup.spec_emote} />
        {isPositionDisplayed && (
          <Tag
            color="default"
            style={{
              paddingRight: calcMargin,
              paddingLeft: calcMargin,
              marginRight: 4,
              marginLeft: 0,
            }}
          >
            {signup.position}
          </Tag>
        )}
        {signup.name} {isNew && <span className="blink">(new)</span>}
      </div>
      {typeof currentRaidId !== "undefined" && (
        <div className="floatingButtons">
          {raids.length >= 3 && currentRaidId !== 0 && (
            <span
              title={`Push to raid ${raids[0]}`}
              onClick={() => updateToRaidIndex(0)}
            >
              1
            </span>
          )}
          {raids.length >= 4 && currentRaidId !== 1 && (
            <span
              title={`Push to raid ${raids[1]}`}
              onClick={() => updateToRaidIndex(1)}
            >
              2
            </span>
          )}
          {raids.length >= 5 && currentRaidId !== 2 && (
            <span
              title={`Push to raid ${raids[2]}`}
              onClick={() => updateToRaidIndex(2)}
            >
              3
            </span>
          )}
          {/* {raids.length >= 6 && currentRaidId !== 3 && (
            <span title={"Push to raid ${""}"} onClick={() => updateToRaidIndex(3)}>
              4
            </span>
          )} */}
          {currentRaidId !== raids.length - 2 && (
            <span
              title={`Push to backup`}
              onClick={() => updateToRaidIndex(raids.length - 2)}
            >
              B
            </span>
          )}
          {currentRaidId !== raids.length - 1 && (
            <span
              title={`Push to bench`}
              onClick={() => updateToRaidIndex(raids.length - 1)}
            >
              X
            </span>
          )}
        </div>
      )}
    </div>
  );
}
