import { LoadingOutlined, SyncOutlined, TagsOutlined } from "@ant-design/icons";
import { Form, Button, Input, FormItemProps, Image, Space } from "antd";
import { useAtom } from "jotai";
import React, { useCallback, useEffect, useState } from "react";
import { RAIDHelper } from "../../../@types/raid-helper";
import { raidIdAtom, usersRaidAtom } from "../../lib/url-state/atoms";

interface Props {
  setRaidHelper: (rh?: RAIDHelper) => void;
}

const CheckRaidHelperApi = () => (
  <>
    (
    <a href="https://raid-helper.dev/" target="_blank" rel="noreferrer">
      check it
    </a>
    )
  </>
);

export function RaidHelperLoader({ setRaidHelper }: Props) {
  const [raidhelperId, setRaidhelperId] = useAtom(raidIdAtom);
  const [, setUsersRaid] = useAtom(usersRaidAtom);
  const [status, setStatus] =
    useState<FormItemProps["validateStatus"]>("error");

  const [statusMessage, setStatusMessage] = useState<React.ReactNode>("");

  const fetchRaid = useCallback(
    async (withReset: boolean = false) => {
      setStatus("validating");
      if (withReset) {
        setUsersRaid("");
      }

      const waitTimeout1 = setTimeout(
        () => setStatusMessage("Raid-Helper takes a long time to answer ..."),
        1500
      );

      const waitTimeout2 = setTimeout(
        () =>
          setStatusMessage(
            <>
              Looks like Raid-Helper API is down ... <CheckRaidHelperApi />
            </>
          ),
        4000
      );

      try {
        const fetchResult = await fetch(
          `https://raid-helper.dev/api/event/${raidhelperId}`
        );
        const raidHelper: RAIDHelper = await fetchResult.json();
        if (raidHelper.status === "failed") {
          setStatusMessage(raidHelper.reason!);
          setRaidHelper(undefined);
          clearTimeout(waitTimeout1);
          clearTimeout(waitTimeout2);
          return;
        }
        setRaidHelper(raidHelper);
        setStatus("success");
        setStatusMessage("");
      } catch (error: any) {
        setStatus("error");
        if (JSON.stringify(error) === "{}") {
          setStatusMessage(
            <>
              The Raid-helper API did not answer, try later{" "}
              <CheckRaidHelperApi />
            </>
          );
        } else {
          setStatusMessage(JSON.stringify(error, null, 2));
        }
        setRaidHelper(undefined);
      }
      clearTimeout(waitTimeout1);
      clearTimeout(waitTimeout2);
    },
    [raidhelperId, setRaidHelper, setUsersRaid]
  );

  useEffect(() => {
    if (raidhelperId.length > 8) {
      fetchRaid();
    } else {
      setRaidHelper(undefined);
    }
  }, [fetchRaid, raidhelperId, setRaidHelper]);

  return (
    <>
      <Space>
        <Form style={{ marginTop: 24 }}>
          <Form.Item validateStatus={status} rules={[{ type: "number" }]}>
            <Input
              style={{ minWidth: 450 }}
              prefix={
                <a
                  href="https://raid-helper.com/"
                  target="_blank"
                  rel="noreferrer"
                >
                  <Image
                    preview={false}
                    width={120}
                    style={{ marginTop: -4 }}
                    src="https://i.imgur.com/CB2TagM.png"
                  />
                </a>
              }
              suffix={
                <>
                  {status === "validating" ? (
                    <div
                      style={{
                        width: 46,
                        height: 28,
                        textAlign: "center",
                        marginTop: 4,
                      }}
                    >
                      <LoadingOutlined />
                    </div>
                  ) : (
                    <Button
                      title="Paste raid-helper ID"
                      style={{ float: "left" }}
                      onClick={() =>
                        navigator.clipboard.readText().then(
                          (cliptext) => setRaidhelperId(cliptext),
                          (err) => console.log(err)
                        )
                      }
                    >
                      {/* <SelectOutlined /> */}
                      <TagsOutlined />
                    </Button>
                  )}

                  <Button
                    title="Resync raid-helper ID"
                    style={{ float: "left" }}
                    onClick={() => fetchRaid(true)}
                  >
                    <SyncOutlined /* spin={status === "validating"}*/ />
                  </Button>
                </>
              }
              size="large"
              value={raidhelperId}
              onChange={(e) => {
                setUsersRaid("");
                setRaidhelperId(e.target.value);
              }}
              placeholder="Your raid-helper id"
            />
          </Form.Item>
        </Form>
        <div>{statusMessage}</div>
      </Space>
    </>
  );
}
